/* FeedbackForm.module.css */
@font-face {
  font-family: "Fixel";
  src: url("./fonts/FixelText-Regular.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}

.title {
  text-align: center;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #fff;
  font-family: "Fixel", sans-serif;
  color: #191919;
}

.formCard {
  box-sizing: border-box;
  width: 100%;
  max-width: 400px;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.formGroup {
  width: 100%; /* Установка ширины группы формы, чтобы соответствовать ширине формы */
  display: flex; /* Для центрирования содержимого по горизонтали */
  flex-direction: column;
  align-items: center; /* Центрирование дочерних элементов */
  margin-bottom: 1rem; /* Отступ снизу для каждой группы */
}

.input,
.submitButton {
  width: 100%; /* Полная ширина внутри своего контейнера */
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  text-align: center; /* Центрирование текста внутри */
}

.input:focus {
  border-color: #a8d300;
  outline: none;
}

.input.error {
  border-color: red;
}

.submitButton {
  padding: 15px 20px;
  background-color: #a8d300;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s, transform 0.1s;
}

.submitButton:hover {
  background-color: #9cc000;
  transform: scale(1.05);
}

.errorMessage {
  color: red;
  font-size: 0.8rem;
  text-align: center;
  margin-top: 5px; /* Небольшой отступ сверху для сообщения об ошибке */
  width: 100%; /* Чтобы центрирование текста работало корректно */
}
